/* Onboarding Styles */
.onboarding-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0);
    backdrop-filter: blur(0px);
    z-index: 10000;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-app-region: no-drag;
    pointer-events: auto;
    padding: 20px;
    box-sizing: border-box;
    animation: overlayFadeIn 0.6s ease-out forwards;
    margin: 0;
    border: none;
    outline: none;
}

@keyframes overlayFadeIn {
    0% {
        background: rgba(0, 0, 0, 0);
        backdrop-filter: blur(0px);
    }

    100% {
        background: rgba(0, 0, 0, 0.9);
        backdrop-filter: blur(20px);
    }
}

.onboarding-modal {
    background: linear-gradient(135deg, rgba(0, 0, 0, 0.95) 0%, rgba(10, 10, 10, 0.95) 100%);
    border: 1px solid rgba(16, 185, 129, 0.4);
    border-radius: 20px;
    padding: 48px;
    max-width: 700px;
    width: 100%;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow:
        0 25px 80px rgba(0, 0, 0, 0.7),
        0 0 0 1px rgba(16, 185, 129, 0.1),
        inset 0 1px 0 rgba(16, 185, 129, 0.1);
    backdrop-filter: blur(20px);
    margin: 0 auto;
    position: relative;
    animation: modalSlideIn 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) forwards;
    transform: translateY(50px) scale(0.9);
    opacity: 0;
}

@keyframes modalSlideIn {
    0% {
        transform: translateY(50px) scale(0.9);
        opacity: 0;
    }

    100% {
        transform: translateY(0) scale(1);
        opacity: 1;
    }
}

.onboarding-header {
    text-align: center;
    margin-bottom: 40px;
    animation: contentFadeIn 0.6s ease-out 0.3s both;
}

@keyframes contentFadeIn {
    0% {
