.start-mapping-button {
    padding: 16px 32px;
    background: linear-gradient(135deg, #10B981 0%, #059669 100%);
    border: none;
    border-radius: 12px;
    color: #000000;
    font-size: 15px;
    font-weight: 600;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: all 0.3s ease;
    box-shadow: 0 4px 16px rgba(16, 185, 129, 0.3);
    min-width: 200px;
    justify-content: center;
}

.start-mapping-button:hover:not(:disabled) {
    box-shadow: 0 6px 24px rgba(16, 185, 129, 0.4);
    transform: translateY(-2px) scale(1.02);
}

.start-mapping-button:disabled {
    opacity: 0.7;
    cursor: not-allowed;
    transform: none;
}

/* Header Controls */
.header-controls {
    display: flex;
    align-items: center;
    gap: 12px;
    -webkit-app-region: no-drag;
    z-index: 100;
    position: relative;
}

.debug-button,
.remap-button {
    padding: 10px 12px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    color: rgba(255, 255, 255, 0.7);
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-app-region: no-drag;
    pointer-events: auto;
    min-width: 40px;
    min-height: 40px;
}

.debug-button:hover,
.remap-button:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #ffffff;
    border-color: rgba(16, 185, 129, 0.4);
    transform: translateY(-1px);
}

.debug-button.active {
    background: rgba(16, 185, 129, 0.2);
    border-color: rgba(16, 185, 129, 0.6);
    color: #10B981;
    box-shadow: 0 0 12px rgba(16, 185, 129, 0.3);
}

/* Action Buttons */
.action-buttons {
    display: flex;
    gap: 12px;
    margin-top: 20px;
}

.execute-button {
    flex: 1;
}

.abort-button {
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 12px;
    color: rgba(255, 255, 255, 0.8);
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    gap: 8px;
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    min-width: 100px;
    position: relative;
    overflow: hidden;
}

.abort-button::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient(135deg, rgba(239, 68, 68, 0.1) 0%, rgba(220, 38, 127, 0.1) 100%);
    opacity: 0;
    transition: opacity 0.3s ease;
}

.abort-button:hover {
    background: rgba(255, 255, 255, 0.12);
    border-color: rgba(239, 68, 68, 0.4);
    color: #ef4444;
    transform: translateY(-1px);
    box-shadow: 0 4px 16px rgba(239, 68, 68, 0.15);
}

.abort-button:hover::before {
    opacity: 1;
}

.abort-button .button-icon,
.abort-button span {
    position: relative;
    z-index: 1;
}

.abort-button:active {
    transform: translateY(0);
    box-shadow: 0 2px 8px rgba(239, 68, 68, 0.1);
}

/* Debug Panel */
.debug-panel {
    background: rgba(0, 0, 0, 0.6);
    border: 1px solid rgba(16, 185, 129, 0.2);
    border-radius: 16px;
    margin: 16px 32px;
    overflow: hidden;
    max-height: 400px;
    display: flex;
    flex-direction: column;
}

.debug-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    background: rgba(16, 185, 129, 0.1);
    border-bottom: 1px solid rgba(16, 185, 129, 0.2);
}

.debug-icon {
    width: 18px;
    height: 18px;
    color: #10B981;
}

.debug-header h3 {
    flex: 1;
    font-size: 16px;
    font-weight: 600;
    color: #ffffff;
}

.close-debug {
    padding: 4px;
    background: transparent;
    border: none;
    color: rgba(255, 255, 255, 0.6);
    cursor: pointer;
    border-radius: 4px;
    transition: all 0.2s ease;
}

.close-debug:hover {
    background: rgba(255, 255, 255, 0.1);
    color: #ffffff;
}

.close-icon {
    width: 16px;
    height: 16px;
}

.debug-content {
    padding: 20px;
    overflow-y: auto;
    flex: 1;
}

.debug-section {
    margin-bottom: 24px;
}

.debug-section h4 {
    font-size: 14px;
    font-weight: 600;
    color: #10B981;
    margin-bottom: 12px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.debug-text {
    background: rgba(0, 0, 0, 0.4);
    border: 1px solid rgba(16, 185, 129, 0.2);
    border-radius: 8px;
    padding: 12px;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
    font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
    overflow-x: auto;
    white-space: pre-wrap;
    max-height: 200px;
    overflow-y: auto;
}

.live-commands-list {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.live-command {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 8px 12px;
    background: rgba(0, 0, 0, 0.4);
    border-radius: 6px;
    border-left: 3px solid #10B981;
}

.command-timestamp {
    font-size: 11px;
    color: rgba(255, 255, 255, 0.5);
    min-width: 80px;
}

.command-text {
    flex: 1;
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
    font-family: 'SF Mono', Monaco, 'Cascadia Code', 'Roboto Mono', Consolas, 'Courier New', monospace;
}

.command-status {
    font-size: 11px;
    padding: 2px 8px;
    border-radius: 12px;
    font-weight: 600;
    text-transform: uppercase;
}

.command-status.running {
    background: rgba(16, 185, 129, 0.2);
    color: #10B981;
}

.command-status.completed {
    background: rgba(34, 197, 94, 0.2);
    color: #22c55e;
}

.command-status.failed {
    background: rgba(239, 68, 68, 0.2);
    color: #ef4444;
}

.no-commands {
    color: rgba(255, 255, 255, 0.5);
    font-style: italic;
    text-align: center;
    padding: 20px;
}

/* Action Buttons Layout Fix */
.action-buttons {
    display: flex;
    gap: 12px;
    margin-top: 20px;
    align-items: center;
}

.action-buttons .execute-button {
    flex: 1;
    min-height: 56px;
    position: relative;
    z-index: 1;
    margin: 0; /* prevent uneven vertical alignment */
}

.action-buttons .abort-button {
    flex-shrink: 0;
    min-height: 56px;
    height: 56px;
    padding: 0 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0; /* align baseline */
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .onboarding-modal {
        padding: 32px 24px;
        margin: 0 auto;
        max-height: 85vh;
        width: calc(100% - 32px);
    }

    .onboarding-header h2 {
        font-size: 28px;
    }

    .onboarding-header p {
        font-size: 16px;
    }

    .onboarding-steps {
        gap: 16px;
    }

    .onboarding-step {
        padding: 20px;
        flex-direction: column;
        text-align: center;
        gap: 16px;
    }

    .onboarding-actions {
        flex-direction: column;
        gap: 12px;
    }

    .skip-button,
    .start-mapping-button {
        width: 100%;
        min-width: auto;
    }

    .action-buttons {
        flex-direction: column;
        gap: 8px;
    }

    .abort-button {
        min-width: auto;
        width: 100%;
    }

    .debug-panel {
        margin: 12px 16px;
        max-height: 300px;
    }

    .header-controls {
        gap: 8px;
    }

    .debug-button,
    .remap-button {
        min-width: 36px;
        min-height: 36px;
        padding: 8px;
    }
